@import url(https://fonts.googleapis.com/css2?family=Cinzel+Decorative:wght@400;700&family=Monoton&family=Mulish:wght@400;700&display=swap);
.mens-window{position:relative;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;width:100%;background-color:#fff;font-family:"Muli", sans-serif}.mens-window .top{position:relative;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;margin-top:10px;height:50px;width:100%;text-align:center}.mens-window .top .mens-logo{position:relative}.mens-window .top .mens-logo .p1{font-family:"Monoton", sans-serif;font-size:23px;color:#985F6F}.mens-window .top .mens-logo .p2{font-family:"Cinzel Decorative", sans-serif;font-size:25px;margin-top:-10px;color:#4E4C67}.mens-window .top .mens-logo .men-avatar{position:absolute;top:30px;right:8px;background:url(data:image/png;base64,) no-repeat center center/cover;height:25px;width:25px}

